.upbook.m-loader.m-loader--right::before {
  right: 0.4em;
}

.main-header {
  color: #5a7da9;
  margin: 20px 0;
  font-weight: 600;
  text-align: center;
  font-size: 3.5rem;
}

@include tablet {
  .main-header {
    font-size: 2.5rem;
  }
}

@include mobile {
  .main-header {
    font-size: 2rem;
  }
}

.m-brand__logo-wrapper {
  img {
    height: 30px;
  }
}

.m-login__logo {
  img {
    width: 70%;
  }
}

.m-login.m-login--2 .m-login__wrapper {
  padding: 1rem 2rem;
}

.m-login.m-login--2 .m-login__wrapper .m-login__container {
  margin: auto;
}

.m-login__btn {
  margin: 10px 5px 0;
  min-width: 140px;
}

.m-header {
  box-shadow: get-shadow(light, lighten1);
}

.member-score .bootstrap-select.form-control {
  max-width: 200px;
}

.no-shadow {
  box-shadow: none;
}

.filter-for-table {
  label {
    margin-bottom: 0;
    margin-right: 7px;
  }
  .bootstrap-select.form-control {
    width: 60%;
  }

  .filter-item {
    margin-bottom: 20px;
  }
}

.mCustomScrollBox {
  min-height: 20px;
}

.hidden-scroll {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  overflow-y: scroll !important;
}

.block-hide {
  opacity: 0;
  transition: all 0.5s;
  &.animated {
    opacity: 1;
  }
}

#score-summary-graph {
  overflow: visible !important;
}

#score-summary-graph .amcharts-chart-div {
  overflow: visible !important;
}

#score-summary-graph .amcharts-chart-div > svg {
  overflow: visible !important;
}

#scheduling-leader-board {
  overflow: visible !important;
}

#scheduling-leader-board .amcharts-chart-div {
  overflow: visible !important;
}

#scheduling-leader-board .amcharts-chart-div > svg {
  overflow: visible !important;
}

#missed-calls-graph {
  margin-top: -25px;
  margin-bottom: -25px;
}

.blockUI {
  &.blockOverlay {
    top: 0 !important;
    left: 0 !important;
    width: 100vw !important;
    height: 100vh !important;
  }

  &.blockMsg.blockPage {
    top: 50% !important;
    left: 50% !important;
    width: auto !important;
    transform: translate(-50%, -50%);

    .m-blockui {
      margin: 0 !important;
    }
  }
}

.info-container {
  position: relative;

  > .fa-info {
    position: absolute;
    right: -15px;
    top: 15px;
    border-radius: 50%;
    background: #5a7da9;
    color: #fff;
    display: block;
    width: 16px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    font-size: 12px;
    cursor: pointer;
  }

  .wrapper__tooltip {
    position: absolute;
    top: 0;
    right: -20px;

    .fa-info {
      position: static;
    }
  }
}

.m-portlet .m-portlet__head-caption.main-header-with-info {
  text-align: center;

  .main-header {
    display: inline-block;
  }

  i {
    top: 0;
    right: -20px;
  }
}

.tooltip.help-tooltip .tooltip-inner {
  max-width: 300px;
  font-size: 1.2em;
}

.header-company-select .dropdown-menu {
  width: 100%;
}

.alert .close {
  content: '';
  padding: 0;
  line-height: 1.3;
}

.m-brand__logo {
  margin-left: 30px;
  min-width: 0;
}

.m-brand__logo-wrapper {
  margin-bottom: 10px;
}

.header-menu {
  flex: 0 0 auto;
}

.practice-name {
  font-size: 2.0em;
  color: #5a7da9;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  .fa {
    font-size: inherit;
    color: #e25a59;
  }
}

.placeholder-block {
  background-color: #eaebf0;
  border-radius: 20px;
}

.placeholder-text {
  background-color: #eaebf0;
  border-radius: 10px;
}

.awesome-label {
  text-align: center;
  width: 100%;
  padding: 0 100px;
  font-size: 4rem;
  transform: skewY(-8deg);
  letter-spacing: 3px;
  word-spacing: -8px;
  color: #e25a59;
  text-shadow: -1px -1px 0 firebrick,
  -2px -2px 0 firebrick,
  -3px -3px 0 firebrick,
  -4px -4px 0 firebrick,
  -5px -5px 0 firebrick,
  -6px -6px 0 firebrick,
  -7px -7px 0 firebrick,
  -8px -8px 0 firebrick,
  -30px 20px 40px dimgrey
}

.m-header .header-menu .dropdown-toggle::after {
  right: 1.5rem;
}

// Tables styles
.table.dataTable {
  table-layout: fixed;
  min-width: 100%;

  th {
    padding-left: 10px !important;
    padding-right: 10px !important;
  }

  tr.odd {
    background-color: #f4f5f8;
  }

  th, td {
    border: none;
  }
}

.dataTables_wrapper {
  overflow-x: auto;
  padding-top: 30px;
}

.dataTables_scrollHead th {
  text-align: center;
  vertical-align: middle !important;
  color: #fff;
}

.dataTables_scrollHead {
  background-color: #5a7da9;
}

.dataTables_scroll {
  padding-top: 30px;
  position: relative;
}

.dataTables_scrollBody {
  border: none !important;
  overflow-y: scroll;
  overflow-x: auto;

  .table {
    thead:first-child {
      position: absolute;
      table-layout: fixed;
      display: table;
      width: 100%;
      z-index: 1;
      top: 0;

      .scroll-gap-cell {
        width: 15px;
        padding: 0 !important;
      }
    }

    td {
      white-space: nowrap;

      &.wrap {
        white-space: normal;
      }
    }
  }
}


#scoreSummaryTable_wrapper .dataTables_scrollBody {
  #scoreSummaryTable {
    thead:first-child {
      position: static;
      table-layout: auto;
      display: table-header-group;
      box-sizing: border-box;
      width: 100%;
      height: 50px;
      z-index: 1;
      top: 0;

      .scroll-gap-cell {
        display: none;
      }
    }

    > td:nth-child(1) {
      width: 70px;
      max-width: 70px;
    }

    > td:nth-child(2) {
      width: 90px;
      min-width: 90px;
      max-width: 90px;
    }

    > td:nth-child(3) {
      width: 105px;
      max-width: 105px;
    }

    > td:nth-child(4) {
      width: 85px;
      min-width: 85px;
      max-width: 85px;
    }

    > td:nth-child(5) {
      width: 80px;
      min-width: 80px;
      max-width: 80px;
    }

    > td:nth-child(6) {
      width: 50px;
      max-width: 50px;
    }

    > td:nth-child(7) {
      width: 70px;
      max-width: 70px;
    }

    > td:nth-child(8) {
      width: 70px;
      max-width: 70px;
    }
  }
}

#callsTable_wrapper .dataTables_scrollHeadInner {
  background-color: #f8c029;

  th {
    color: inherit;
  }
}

#callsTable tr td:last-of-type {
  cursor: pointer;
}

.practice-notes {
  font: inherit;
  color: #444;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
  margin-bottom: 0;
}

.practice-notes.placeholder {
  color: #848484;
}

.read-more-btn {
  border: none;
  background-color: transparent;
  outline: none !important;
  color: #5a7da9;
  font-weight: bold;
  float: right;
  cursor: pointer;
}

#scoreSummaryTable {
  tr.booked {
    background: #c5f3d233;
  }

  tr.no-booked {
    background: #f72e2033;
  }

  .fa-star {
    margin-right: 5px;
  }

  .tooltip-table td {
    padding: 0;
    border: none;
  }

  .tooltip-permissions {
    width: 150px;
    height: 85px;
  }
}

#skillsGradeTable_wrapper,
#eliteScoreTable {
  th, td {
    font-size: 1.3rem;
  }
}

#eliteScoreTable {
  thead tr {
    background-color: #5a7da9;
  }

  th {
    text-align: center;
    color: #fff;
  }

  th, td {
    vertical-align: middle;
  }

  td {
    color: #5a7da9;
  }

  .first-letter-bold::first-letter {
    font-weight: 600;
  }

  .score-letter {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 600;
  }
}

// Initial animation
.rebooking-opportunities-animated-wrapper,
.score-summary-animated-wrapper,
.calling-practice-animated-wrapper {
  opacity: 0;
  transition: all 1s;

  &.animated {
    opacity: 1;
  }

  table {
    margin-top: 11px;
  }
}
// Tables styles

.initials-input {
  width: 70px;
  display: inline-block;
  margin-left: 10px;
}

.feature-button {
  position: fixed;
  left: 30px;
  bottom: 20px;
  width: 60px;
  height: 60px;
  z-index: 99999;
  border-radius: 50%;
  border: none;
  outline: none !important;
  cursor: pointer;
  background-color: #84bb41;
  padding: 7px;

  img {
    width: 100%;
    height: 100%;
  }
}

// PDF styles
.pdf {
  width: 11in;
  -webkit-print-color-adjust: exact;
  background-color: #fff !important;

  .m-body {
    background-color: #fff;
  }

  svg {
    overflow: visible;
  }

  .m-portlet {
    margin-bottom: 0;
  }

  #mainHeader {
    line-height: 3rem;
  }

  #new-client-scheduling-rate {
    margin-bottom: -50px;
  }

  #missed-calls-graph {
    margin-bottom: -20px;
    text-align: center !important;
  }

  #skillsGradeTable_wrapper {
    th, td {
      font-size: inherit;
    }
  }

  .page2 {
    page-break-before: always;
  }
}

// Override bootstrap styles
@media print {
  // Force landscape orientation
  @page {
    size: landscape;
  }

  .table td, .table th {
    background-color: transparent !important;
  }

  .table {
    border-collapse: separate !important;
  }
}
// PDF styles
