@import 'config';

// Global styles
.stacked-columns-chart-tooltip {
  font-family: $font-family;
  color: $c-gray-main;
  padding: 3px 5px 10px;

  .stacked-columns-chart-tooltip-row {
    display: flex;
    align-items: center;
    font-size: 12px;
    letter-spacing: -0.5px;
    padding: 5px 0 0;
    margin: 0;

    .tooltip-count,
    .tooltip-day {
      margin-left: 6px;
      margin-top: -1px;
      font-family: 'SF UI Text', 'Open Sans', sans-serif;
      font-size: 16px;
      font-weight: 600;
    }

    .tooltip-day {
      margin-left: 0;
    }
  }
}
